import React from "react";
import EventTime from "./EventTime";
export default class Curicullum extends React.Component {
  render() {
    return (
      <section
        className="box-timeline py-5 mb-5 position-relative"
        style={{ "z-index": "1200" }}
        id="banner"
      >
        <img src="/assets/images/background/video-one-bg.png" alt="Awesome Image" className="video-one__bg"/>
        <div className="header-timeline">
          <div className="block-title text-center">
            <h2 className="block-title__title">
              <span> Kurikulum </span>{" "}
            </h2>{" "}
          </div>{" "}
          
        </div>{" "}
        <div className="container">
        <div className="tabs">
            <Tabs>
              <Tab label="Hari 1">
                <div>
                 <EventTime/>
                  <p>Tab 1 content</p>
                </div>
              </Tab>
              <Tab label="Hari 2">
                <div>
                  <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ2E3SrDqYWF9J_TDWNzjZkwJOAGxkLJVZPvUly7SqUL9WqLqEw" />
                  <p>Tab 2 content</p>
                </div>
              </Tab>
              <Tab label="Hari 3">
                <div>
                  <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRB7Y713wstirF-PvKJa6OioS83ozc-BeFAzjjyndv58ZnfyaZK0g" />
                  <p>Tab 3 content</p>
                </div>
              </Tab>
            </Tabs>
        </div>
        </div>
      </section>
    );
  }
}

class Tabs extends React.Component {
  state = {
    activeTab: this.props.children[0].props.label,
  };
  changeTab = (tab) => {
    this.setState({ activeTab: tab });
  };
  render() {
    let content;
    let buttons = [];
    return (
      <div>
        {React.Children.map(this.props.children, (child) => {
          buttons.push(child.props.label);
          if (child.props.label === this.state.activeTab)
            content = child.props.children;
        })}

        <TabButtons
          activeTab={this.state.activeTab}
          buttons={buttons}
          changeTab={this.changeTab}
        />
        <div className="tab-content">{content}</div>
      </div>
    );
  }
}

const TabButtons = ({ buttons, changeTab, activeTab }) => {
  return (
    <div className="tab-buttons">
      {buttons.map((button) => {
        return (
          <button
            className={
              button === activeTab ? "tab-button active" : "tab-button"
            }
            onClick={() => changeTab(button)}
          >
            {button}
          </button>
        );
      })}
    </div>
  );
};

const Tab = (props) => {
  return <React.Fragment>{props.children}</React.Fragment>;
};
